import React from "react";
import { Outlet, Link, useLoaderData } from "react-router-dom";
import { AppUser, getCurrentUser } from "../utils/firebase/auth";

type RootLoaderData = {
  user: AppUser;
};

const Root = () => {
  const { user } = useLoaderData() as RootLoaderData;

  return (
    <div className="App">
      <header className="App-header">
        <Link className="App-link" to="/">
          Home
        </Link>
        {user ? (
          <>
            <Link className="App-link" to="/profile">
              {user.email}
            </Link>
          </>
        ) : (
          <>
            <Link className="App-link" to="/register">
              Register
            </Link>
            <Link className="App-link" to="/login">
              Login
            </Link>
          </>
        )}
      </header>
      <div className="App-page">
        <Outlet />
      </div>
    </div>
  );
};

export const rootLoader = async (): Promise<RootLoaderData> => {
  const user = await getCurrentUser();
  return { user };
};

export default Root;
